import React from "react";
import StackGrid from "react-stack-grid";

const Explore = () => {
  return (
    <StackGrid
      columnWidth={
        window.innerWidth > 768
          ? (window.innerWidth - 10) / 3
          : (window.innerWidth - 20) / 1
      }
      monitorImagesLoaded={true}
      itemComponent="div"
      gutterWidth={20}
      gutterHeight={20}
      // horizontal={true}
    >
      <div key="key1" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>

      <div key="key2" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key3" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key4" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key5" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key6" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key7" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key8" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key9" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key10" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key11" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key12" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
      <div key="key13" className="image">
        <img src="https://picsum.photos/450/450" alt="Something" />
      </div>
    </StackGrid>
  );
};

export default Explore;
