import React from "react";
import StackGrid from "react-stack-grid";
import "../App.css";
import Pin from "./Pin";

const Home = () => {
  const src_link = [
    "https://picsum.photos/300/400",
    "https://picsum.photos/300/280",
    "https://picsum.photos/300/410",
    "https://picsum.photos/300/350",
    "https://picsum.photos/300/320",
    "https://picsum.photos/300/200",
    "https://picsum.photos/300/250",
    "https://picsum.photos/300/360",
    "https://picsum.photos/300/540",
    "https://picsum.photos/300/600",
    "https://picsum.photos/300/200",
    "https://picsum.photos/300/510"
  ];
  return (
    <StackGrid
      columnWidth={
        window.innerWidth > 768
          ? (window.innerWidth - 40) / 5
          : (window.innerWidth - 20) / 2
      }
      monitorImagesLoaded={true}
      itemComponent="div"
      gutterWidth={20}
      gutterHeight={20}
      // horizontal={true}
    >
      {src_link.map((link, index) => (
        <Pin
          source={link}
          key={"Div-" + index}
          id={"Div-" + index}
          style={{ row_no: 100 }}
        />
      ))}
    </StackGrid>
  );
};

export default Home;
