import { React, useState } from "react";
import { Navbar, Container } from "react-bootstrap";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Brand from "./Brand";
import CreatePin from "./Create";
import Menu from "./Menu";
import Home from "./Home";
import Explore from "./Explore";
import PinCreation from "./PinCreation";
import PageNotFound from "./Pnf";
import SlideShow from "./Slideshow";
import PinForm from "./PinForm";
// import Menu from './New'

const NavbarComponent = () => {
  const [modalShow, setModalShow] = useState(true);
  const show = () => {
    setModalShow(true);
  };
  const hide = () => {
    setModalShow(false);
  };
  return (
    <div>
      <Router>
        <Navbar variant="dark">
          <Container>
            <Navbar.Brand href="#home">
              <Brand />
            </Navbar.Brand>
            <Menu />
          </Container>
        </Navbar>

        <div>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/explore" element={<Explore />} />
            <Route
              path="/pinCreation"
              element={<CreatePin show={modalShow} onHide={hide} />}
            />
            <Route path="/pinCreationForm/:url" element={<PinCreation />} />
            <Route path="/slideshow" element={<SlideShow />} />
            <Route path="/pinform" element={<PinForm />} />
            <Route path="*" element={<PageNotFound />} />
          </Routes>
        </div>
      </Router>
    </div>
  );
};
export default NavbarComponent;
