import React from "react";
import { Form, Row, Col } from "react-bootstrap";
import Floating from "./Floating";
import { minutes, hours, serving } from "./optionList";
const Ingridents = () => {
  const createOption = (el, id) => {
    return <option value={id}>{el}</option>;
  };
  console.log(hours);
  console.log(serving);
  return (
    <div className="m-5">
      <label for="time">Cooking Time</label>
      <Row>
        <Col>
          <Form.Select aria-label="Floating label select example">
            {hours.map(createOption)}
          </Form.Select>
        </Col>
        <Col>
          <Form.Select aria-label="Floating label select example">
            {minutes.map(createOption)}
          </Form.Select>
        </Col>
      </Row>

      <label for="serving">Serving</label>
      <Form.Select aria-label="Floating label select example">
        {serving.map(createOption)}
      </Form.Select>
      <Floating
        label="Ingrident"
        type="textarea"
        placeholder="Ingredients" //if time then automate the list on enter
      />
    </div>
  );
};

export default Ingridents;
