import React, { useState } from "react";
import { Form } from "react-bootstrap";
import "../App.css";
const Floating = (props) => {
  const [input, setInput] = useState(false);

  const checktype = (e) => {
    console.log("Whats");
    console.log(props.type);
    if (props.type === "textarea") {
      e.target.setAttribute("as", "textarea");
      e.target.setAttribute("id", "textarea");
    } else {
      e.target.setAttribute("type", props.type);
    }
  };
  const handleFocus = () => {
    setInput(true);
  };
  const handleBlur = () => {
    setInput(false);
  };
  return (
    <div className="floating-form">
      <label
        style={{
          display: input ? "block" : "none"
        }}
        className="floating-label"
      >
        {props.label}
      </label>
      <Form.Control
        id={props.id}
        placeholder={input ? props.placeholder : props.label}
        onClick={checktype}
        onFocus={handleFocus}
        onBlur={handleBlur}
        className="floating-main"
      />
    </div>
  );
};

export default Floating;
