import React, { useState } from "react";
import PinHeader from "./PinHeader";
import PinFooter from "./PinFooter";
const Pin = (props) => {
  const [hover, setHover] = useState(false);
  const [unauthorized, setAuthorization] = useState(false);

  // const width = (window.innerWidth - 40) / 5;

  const r_no = props.style.row_no;
  const style = {
    marginTop:
      r_no === 0
        ? props.style.margin < 4
          ? 80 * props.style.margin + "px"
          : 80 * (6 - props.style.margin) + "px"
        : "None"
  };

  console.log(props.source.slice(22, 25));
  const handleMouseIn = (e) => {
    const sc = document.getElementById(props.id);
    const height = e.target.getAttribute("height");
    const width = e.target.getAttribute("width");
    sc.setAttribute("class", "hover");
    sc.setAttribute(
      "style",
      "--height:" + height + "px;--width:" + width + "px"
    );
    setHover(!hover);
  };

  const handleMouseOut = () => {
    const sc = document.getElementById(props.id);
    sc.removeAttribute("class");
    setHover(!hover);
  };

  return (
    <div
      className="image"
      onMouseEnter={unauthorized ? null : handleMouseIn}
      onMouseLeave={unauthorized ? null : handleMouseOut}
      style={style}
    >
      {hover ? <PinHeader /> : null}
      <div id={props.id}></div>
      <img
        src={props.source}
        alt="Pin"
        height={props.source.slice(26)}
        width={props.source.slice(22, 25)}
      />
      {hover ? <PinFooter /> : null}
    </div>
  );
};

export default Pin;
